@mixin on-desktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin on-tablet {
  @media (min-width: 744px) {
    @content;
  }
}

@mixin on-ipad-mini {
  @media (min-width: 438px) {
    @content;
  }
}

@mixin flex($type: flex, $align: false, $justify: false, $direction: false) {
  display: $type;

  @if $align {
    align-items: $align;
  }

  @if $justify {
    justify-content: $justify;
  }

  @if $direction {
    flex-direction: $direction;
  }
}

@mixin fonts($font-size, $line-height, $font-weight) {
  font-size: $font-size;
  line-height: $line-height;
  font-weight: $font-weight;
}

@mixin sizes($width, $height) {
  width: $width;
  height: $height;
}

@mixin background($repeat, $position, $size) {
  background-repeat: $repeat;
  background-position: $position;
  background-size: $size;
}

@mixin top-actions {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
}

@mixin hover-photo {
  transform: scale(107%);
  filter: contrast(105%);
}
